body {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #1f2029;
  background-color: #fff;
  background-image: url('https://assets.codepen.io/1462889/back-page.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 101%;
}

p {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #1f2029;
}

.section {
  position: relative;
  width: 100%;
  display: block;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked) {
  position: absolute;
  left: -9999px;
}

.account-btn:checked+label,
.account-btn:not(:checked)+label {
  position: absolute;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 2;
  height: 50px;
  transition: all 200ms linear;
  border-radius: 4px;
  width: 240px;
  letter-spacing: 1px;
  display: inline-flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  -moz-justify-content: center;
  -ms-justify-content: center;
  justify-content: center;
  -ms-flex-pack: center;
  text-align: center;
  align-self: center;
  border: none;
  cursor: pointer;
  background-color: #102770;
  color: #ffeba7;
  box-shadow: 0 12px 35px 0 rgba(16, 39, 112, .25);
}

.account-btn:not(:checked)+label:hover {
  background-color: #ffeba7;
  color: #102770;
}

.account-btn:checked+label .uil,
.account-btn:not(:checked)+label .uil {
  margin-left: 10px;
  font-size: 18px;
}

.account-btn:checked+label:after,
.account-btn:not(:checked)+label:after {
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 110;
  width: 40px;
  border-radius: 3px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  background-color: #ffeba7;
  color: #102770;
  font-family: 'unicons';
  content: '\eac6';
  box-shadow: 0 12px 25px 0 rgba(16, 39, 112, .25);
  transition: all 200ms linear;
  opacity: 0;
  pointer-events: none;
  -webkit-transform: translateY(20px);
  transform: translateY(20px);
}

.account-btn:checked+label:hover:after,
.account-btn:not(:checked)+label:hover:after {
  background-color: #102770;
  color: #ffeba7;
}

.account-btn:checked+label:after {
  transition: opacity 300ms 300ms ease, background-color 250ms linear, color 250ms linear, -webkit-transform 300ms 300ms ease;
  transition: opacity 300ms 300ms ease, transform 300ms 300ms ease, background-color 250ms linear, color 250ms linear;
  transition: opacity 300ms 300ms ease, transform 300ms 300ms ease, background-color 250ms linear, color 250ms linear, -webkit-transform 300ms 300ms ease;
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.modal {
  position: fixed;
  display: block;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow-x: hidden;
  background-color: rgba(31, 32, 41, .75);
  pointer-events: none;
  opacity: 0;
  transition: opacity 250ms 700ms ease;
}

.account-btn:checked~.modal {
  pointer-events: auto;
  opacity: 1;
  transition: all 300ms ease-in-out;
}

.modal-wrap {
  position: relative;
  display: block;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
  overflow: hidden;
  padding-bottom: 20px;
  background-color: #fff;
  align-self: center;
  box-shadow: 0 12px 25px 0 rgba(199, 175, 189, .25);
  opacity: 0;
  -webkit-transform: scale(0.6);
  transform: scale(0.6);
  transition: opacity 250ms 250ms ease, -webkit-transform 300ms 250ms ease;
  transition: opacity 250ms 250ms ease, transform 300ms 250ms ease;
  transition: opacity 250ms 250ms ease, transform 300ms 250ms ease, -webkit-transform 300ms 250ms ease;
}

.modal-wrap img {
  display: block;
  width: 100%;
  height: auto;
}

.modal-wrap p {
  padding: 20px 30px 0 30px;
}

.account-btn:checked~.modal .modal-wrap {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: opacity 250ms 500ms ease, -webkit-transform 350ms 500ms ease;
  transition: opacity 250ms 500ms ease, transform 350ms 500ms ease;
  transition: opacity 250ms 500ms ease, transform 350ms 500ms ease, -webkit-transform 350ms 500ms ease;
}

.logo {
  position: absolute;
  top: 25px;
  left: 25px;
  display: block;
  z-index: 1000;
  transition: all 250ms linear;
}

.logo img {
  height: 26px;
  width: auto;
  display: block;
  -webkit-filter: brightness(10%);
  filter: brightness(10%);
  transition: -webkit-filter 250ms 700ms linear;
  transition: filter 250ms 700ms linear;
  transition: filter 250ms 700ms linear, -webkit-filter 250ms 700ms linear;
}

.account-btn:checked~.logo img {
  -webkit-filter: brightness(100%);
  filter: brightness(100%);
  transition: all 250ms linear;
}

@media screen and (max-width: 500px) {
  .modal-wrap {
    width: calc(100% - 40px);
    padding-bottom: 15px;
  }

  .modal-wrap p {
    padding: 15px 20px 0 20px;
  }
}

